.header {
  background-image: url(../images/header-bg-mobile2.png);
  box-sizing: border-box;
  height: 100vh;
  background-size: cover;
  background-position: right;

  @include onTablet {
    background-image: url(../images/Cover_photo.png);
    background-position: center;
  }

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;
    text-align: center;

    @include onTablet {
      font-size: 64px;
      line-height: 64px;
    }
  }

  &__bottom {
    padding-bottom: 88px;
    @include constentPaddingInline;

    @include onTablet {
      padding-bottom: 130px;
    }
  }
}
